import { render } from '../render';
import FilterView from '../view/filter-view';
import NewEventButtonView from '../view/new-event-button-view';
import TripInfoView from '../view/trip-info-view';

export default class TripPresenter {
  #tripInfoContainer = null;
  #tripModel = null;
  #filterContainer = null;
  #filter = new FilterView();
  #tripInfo = new TripInfoView();
  #newEventButton = new NewEventButtonView();

  constructor(tripInfoContainer, filtersContainer, tripModel) {
    this.#tripInfoContainer = tripInfoContainer;
    this.#filterContainer = filtersContainer;
    this.#tripModel = tripModel;
  }

  init() {
    render(this.#tripInfoContainer, this.#tripInfo);
    render(this.#filterContainer, this.#filter);
    render(this.#tripInfoContainer, this.#newEventButton);
  }
}
