import TripModel from './model/trip-model';
import BoardPresenter from './presenter/board-presenter';
import TripPresenter from './presenter/trip-presenter';

const tripInfoContainer = document.querySelector('.trip-main');
const filtersContainer = tripInfoContainer.querySelector('.trip-controls__filters');
const contentContainer = document.querySelector('.trip-events');

const tripModel = new TripModel();
tripModel.init();

const trip = new TripPresenter(tripInfoContainer, filtersContainer, tripModel);
trip.init();

const routeDesk = new BoardPresenter(contentContainer, tripModel);
routeDesk.init();
